body {
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui,
    helvetica neue, helvetica, Ubuntu, roboto, noto, arial, sans-serif;
  font-style: normal;
  line-height: 1.2;
  margin: 0;
  overflow: hidden;
  position: relative;
}

video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  opacity: 0.5;
  pointer-events: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

li {
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

h1,
h2 {
  margin-bottom: 25px;
}

.title {
  margin-bottom: 15px;
}

ul {
  list-style: none;
}
